import React, { useEffect, useState } from "react"
import {Container,Col,Row,Image, ListGroup, ListGroupItem,Card} from "react-bootstrap"

import Venom_Card from "../component/venomcard"
import report from "../assets/report.png"
import {reactLocalStorage} from 'reactjs-localstorage';
import { MdIndeterminateCheckBox } from "react-icons/md";


function Home(){

    const [list,setList]=useState([]) 
    const [filter,setFilter]=useState(false) 
    const [filterIndex,setFilterIndex]=useState("")
    
    var division=[]

    

    useEffect(()=>{
       Call_Api()
      
           
        
        function Call_Api(){
             var requestOptions = {
            method: 'GET',
            redirect: 'follow'
          };
          
          fetch("https://raw.githubusercontent.com/aungmyominoffical/supportmyanmar/main/list.json", requestOptions)
            .then(response => response.json())
            .then(result => {
               
                setList(result)
                
                

               

            })
            .catch(error => console.log('error', error));

        
        }

        


       
    },[0])

    function Find_Index(props){

        if(props.index == filterIndex){
            return true
        }else{

            return false
        }
    }


    
  

    return(
        <div>
             <Container >
            <Row >
                <Col sm={4} style={{marginTop:"20px"}}>
                    <Col style={{height:"100px",width:"100%",backgroundColor:"red",borderRadius:"10px"}}
                    onClick={()=>{
                        window.open("https://forms.gle/sYCB3oUSqwkKfBp19","_blank")
                    }}
                    >
                        <Image src={report} style={{height:"100px",width:"100%"}} />
                    </Col>
                    <div style={{marginTop:"10px"}}>
                       
                        <ListGroup>
                            <ListGroupItem 
                            style={{fontWeight:"bold"}}
                            onClick={()=>{
                                setFilter(false)
                            }}
                            >All</ListGroupItem>
                            {
                              
                                   
                                   list.map((e)=>{
                                    if(division.includes(e.division) == false){
                                        division.push(e.division)
                                    }

                                    
                                   })
                                   
                                  
                            
                                
                            }
                            {
                                division.map((item,index)=>{
                                    return <ListGroupItem 
                                    style={{fontWeight:"bold"}}
                                    onClick={()=>{
                                        setFilter(true)
                                        setFilterIndex(item)
                                    }}
                                    >
                                        {item}
                                    </ListGroupItem>
                                })
                            }

                        </ListGroup>
                    
                    </div>
                </Col>
                <Col sm={8} style={{marginTop:"20px"}}>
                   {
                       filter? <Container style={{margin:"3px"}}>
                           <Row style={{borderRadius:"5px",padding:"5px",backgroundColor:"gold",height:"40px",}}>
                           <p style={{color:"white",fontWeight:"bold"}} >Find By Division -</p>
                           <p style={{color:"red",fontWeight:"bold"}}>{filterIndex}</p>

                           </Row>
                          
                       
                   </Container>:<div></div>
                   }
                
                    {
                        filter?<div>
                            {
                                list.map((item)=>{
                                    if(item.division == filterIndex){
                                    return  <Venom_Card 
                                    profile={item.profile}
                                    name={item.name}
                                    position={item.position}
                                    degree={item.degree}
                                    location={item.location}
                                    parameter={item.parameter}
                                    />
                                    }
                                })
                            }
                        </div>:
                        <div>{
                            
                            list.map((item)=>{
                                return  <Venom_Card 
                                profile={item.profile}
                                name={item.name}
                                position={item.position}
                                degree={item.degree}
                                location={item.location}
                                parameter={item.parameter}
                                />
                            })}</div>
                    }

                </Col>
            </Row>
        </Container>

        </div>
       
    );
}

export default Home